/* src/styles/index.css */
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
@import url('https://fonts.googleapis.com/css2?family=Kolker+Brush&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Protest+Revolution&display=swap');

.articles {
    font-family: 'Noto Sans', sans-serif;
    font-size: 14px;
    font-weight: 300;
    
}

.artitle {
    font-family: 'Inter', sans-serif;
}

#fulcrum-body, #pproject-body {
    font-family: 'Noto Sans', sans-serif;
    font-size: 14px;
    font-weight: 300;
    text-align: justify;
}

#pproject-body h2,h1 {
    font-family: 'Inter', sans-serif;
    font-size: 21px;
    font-weight: 600;
    padding-top:5px;
    padding-bottom:5px;
}

#fulcrum-body h3, #pproject-body h3 {
    font-family: 'Inter', sans-serif;
    font-size: 21px;
    font-weight: 600;
    padding-top:15px;
    padding-bottom:5px;
}

#fulcrum-body ol, #pproject-body ul {
    list-style: none;
    padding: 0;
    margin: 5px;
    font-style: italic;
  }
  
  #fulcrum-body ol li::before, #pproject-body ul li::before {
    content: '\2022';
    color: #f72274;
    font-size: 1.2em;
    margin-right: 10px;
  }

.parent-container {
  height: 100vh;
  width: 100vw;
  font-family: 'Bebas Neue', sans-serif;
}

.panel {
  display: flex;
  height: 100vh;
  position:relative;
}


  
